.container {
    padding: 0 13px;
    height: 100%;
}

.monthView {
    width: 100%;
    height: 85%;
    display: flex;
    flex-direction: column;
}

.weekNames {
    display: flex;
    background-color: #279cef;
    color: white;
}

.weekName {
    text-align: center;
    margin: 5px;
    width: calc(100% / 7);
}

.week {
    display: flex;
    width: 100%;
    min-height: 120px;
    height: 150px;
}

.day {
    width: calc(100% / 7);
    border: 1px solid lightgray;
    margin: 5px;
    padding: 5px;
    user-select: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    word-wrap: break-word;
}

.dayHover:hover {
    background-color: #e5ffff;
    box-shadow: 2px 3px #eee;
}

.currDay {
    background-color: #b9daff;
    color: black;
}


.disableDay {
    color: #bbb;
}

.slotsContainer {
    overflow-y: auto;
    width: 100%;
}

.slots {
    word-break: break-all;
}

.slot {
    font-size: 13px;
    margin: 3px 2px;
    padding: 3px 4px;
    border-radius: 2px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    min-height: 100px;
}

.detail1 {
    display: flex;
    width: 100%;
    align-items: center;
}

.detail2 {
    display: flex;
    width: 100%;
}

.detail2 span {
    margin-left: auto;
}

.name {
    margin-right: 15px;
    font-weight: 600;
    font-size: 16px;
    margin-top: 8px;
}

.time {
    font-size: 12px;
    font-weight: 500;
    color: snow;
}

.delete{
    margin-left: auto;
    cursor: pointer;
    
}

.delete:hover{
    color: red;
}

.topic {
    font-size: 14px;
}